import React from 'react';
import style from './Main.module.scss';
import styleContainer from '../common/styles/Container.module.css'
import {Title} from "../common/components/title/Title";
import Photo from '../assets/image/photo_2021-10-26_12-55-34.jpg'



export function Main() {

    const MyPhoto= `url(${Photo})`;

    return (
        <div className={style.mainBlock}>
            <div className={styleContainer.container}>
                <div className={style.greetings}>
                    <span className={style.description}>Hello there</span>
                    <Title text={`I'm Pavel Radzishevskii`}/>
                    <span className={style.description}>Frontend Developer.</span>
                </div>
                <div className={style.photo} style={{backgroundImage: MyPhoto}} >
                </div>
            </div>
        </div>
    );
}


